<template>
  <div class="page flex-col">
    <div class="cont flex-col justify-between align-center">
      <!-- <img src="../assets/set_user/guide_head1@2x.png" alt="" /> -->
      <div class="top-head">
        <!-- <div class="head-box" v-for="(item, index) in headView" :key="index"> -->
        <div
          :class="['head-box', 2 === index ? 'h-active' : '']"
          v-for="(item, index) in headList"
          :key="index"
        >
          <img :src="item.url" class="head" />
        </div>
      </div>
      <div class="group1 flex-row justify-between">
        <div class="left align-end">
          <div class="arrow" @click="changeActive('left')">
            <img src="../assets/set_user/arr_l@2x.png" alt="" />
          </div>
        </div>
        <div class="mid">
          <div class="person flex-col justify-end align-center pr">
            <div class="role flex-col justify-end align-center">
              <img :src="userList[userActive].url" alt="" />
              <!-- <img src="../assets/set_user/user1@2x.png" alt="" /> -->
            </div>
            <div class="pan justify-center">
              <img src="../assets/set_user/pan@2x.png" alt="" />
            </div>
          </div>
          <div class="name">
            <img :src="userList[userActive].nameurl" alt="" />
          </div>
        </div>
        <div class="right align-end">
          <div class="arrow" @click="changeActive('right')">
            <img src="../assets/set_user/arr_r@2x.png" alt="" />
          </div>
        </div>
      </div>
      <div class="group2">
        <div @click="confirmIdentity" class="btn"></div>
      </div>
    </div>
    <div class="mask-layer" v-show="showGuide">
      <div class="mask-con">
        <img @click="hideGuide" src="../assets/set_user/guide@2x.png" />
      </div>
    </div>
    <Modal class="modal" :title="modalTit" :visible.sync="isModalShow">
      <div class="content" slot="content">
        <input
          class="input"
          type="text"
          v-model="nick_name"
          placeholder="请输入2-16个字符的昵称"
        />
      </div>
      <div class="footer" slot="footer">
        <img
          @click="confirmNickname"
          src="@/assets/set_user/btn-comfirm@2x.png"
          alt=""
        />
      </div>
    </Modal>
  </div>
</template>

<script>
import Modal from '@/components/commonModal'
export default {
  components: {
    Modal
  },
  data () {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')).data,
      showGuide: true,
      userActive: 2,
      headList: [
        {
          url: './static/img/user/thead4@2x.png'
        },
        {
          url: './static/img/user/thead5@2x.png'
        },
        {
          url: './static/img/user/thead1@2x.png'
        },
        {
          url: './static/img/user/thead2@2x.png'
        },
        {
          url: './static/img/user/thead3@2x.png'
        }

      ],
      userList: [
        {
          url: './static/img/user/guser4@2x.png',
          nameurl: './static/img/user/name4@2x.png'
        },
        {
          url: './static/img/user/guser5@2x.png',
          nameurl: './static/img/user/name5@2x.png'
        },
        {
          url: './static/img/user/guser1@2x.png',
          nameurl: './static/img/user/name1@2x.png'
        },
        {
          url: './static/img/user/guser2@2x.png',
          nameurl: './static/img/user/name2@2x.png'
        },
        {
          url: './static/img/user/guser3@2x.png',
          nameurl: './static/img/user/name3@2x.png'
        }

      ],
      isModalShow: false,
      modalTit: '请输入个人昵称',
      isNewer: true,
      nick_name: ''
    }
  },
  created () {
    this.isNewer = this.$store.getters.isNewer
    console.log(this.isNewer)
    if (this.isNewer) {
      this.showGuide = true
    } else {
      this.showGuide = false
    }
    this.headView = this.headList
  },
  methods: {
    // 隐藏引导
    hideGuide () {
      this.showGuide = false
    },
    changeActive (type) {
      // console.log(type)
      // console.log(this.userActive)
      if (type && type === 'right') { // 右
        if (this.userActive + 1 <= this.headList.length - 1) {
          this.userActive += 1
        } else {
          this.userActive = 0
        }

        let temp = this.headList.shift()
        this.headList.push(temp)
        temp = null
      } else { // 左
        if (this.userActive - 1 >= 0) {
          this.userActive -= 1
        } else {
          this.userActive = this.headList.length - 1
        }

        let temp = this.headList.pop()
        this.headList.unshift(temp)
        temp = null
      }
      console.log(this.userActive)
    },
    // 确认身份
    confirmIdentity () {
      this.isModalShow = true
    },
    async confirmNickname () {
      var reg = /^[a-zA-Z0-9_\u4e00-\u9fa5]+$/// 中文，数字，字母，下划线
      if (this.computedStrLen(this.nick_name) > 16 || this.computedStrLen(this.nick_name) < 2) {
        this.Toast('请输入2-16个字符(1个汉字2个字符)')
        return
      } else if (!reg.test(this.nick_name)) {
        this.Toast('备注名称中不能包含特殊符号')
        return
      }
      // 1-李白，2-李清照，3-信小福，4-关羽，5-花木兰
      let role = ''
      if (this.userActive > 1) {
        role = this.userActive - 1
      } else {
        role = this.userActive + 4
      }
      // console.log(role)

      this.isModalShow = false
      let res = await this.$request('post', 'user/createrole', {
        uid: this.userInfo.uid, nick_name: this.nick_name, role: role
      })
      // console.log(res)
      if (res && res.error_no === '0') {
        this.$store.commit('setUserInfo', res.data)
        setTimeout(() => {
          this.$router.push({ name: 'home', params: { headIndex: this.userActive } })
        }, 100)
      }
    },

    // 计算字符串长度，英文1个字符，中文2个字符
    computedStrLen: function (str) {
      var len = 0
      for (var i = 0; i < str.length; i++) {
        var c = str.charCodeAt(i)
        // 单字节加1
        if ((c >= 0x0001 && c <= 0x007e) || (c >= 0xff60 && c <= 0xff9f)) {
          len++
        } else {
          len += 2
        }
      }
      return len
    }
  }
}
</script>

<style lang="less" scoped>
@import url("../assets/css/mixin.less");
.page {
  position: relative;
  // width: 100%;
  width: 100%;
  height: 100%;
  // min-height: calc(100vh - 8vw);
  overflow-y: auto;
  .bg-image("../assets/set_user/bg","png");

  .cont {
    z-index: 3;
    position: relative;
    width: 100%;
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
  }
}
.top-head {
  width: calc(100% + 100px);
  margin: 0 -50px;
  height: 264px;
  padding: 0 20px 20px;
  .bg-image("../assets/set_user/bg2","png");
  .flex_func(row, center, center, nowrap);
}
.head-box {
  width: 160px;
  height: 160px;
  .flex_func(row, center, center, nowrap);
  .head {
    width: 100%;
    height: 100%;
  }
}
.h-active {
  width: 192px;
  height: 192px;
}
.group1 {
  width: 100%;
  height: calc(100% - 524px);

  .left {
    width: 132px;
    height: 100%;
    padding: 0 0 182px 44px;
  }
  .mid {
    width: calc(100% - 264px);
    height: 100%;
    .person {
      width: 100%;
      height: calc(100% - 98px);
      // .bg-image("../assets/set_user/pan","png");
      // background-size: 454px 122px;
      .role {
        width: calc(100% + 124px);
        // height: calc(100% - 85px);
        height: calc(100% - 61px);
        margin-right: -124px;
        margin-bottom: -61px;
        z-index: 2;
        > img {
          width: 560px;
          height: 506px;
        }
      }
      .pan {
        width: 100%;
        height: 122px;
        margin-bottom: 20px;
        // background: pink;
        // justify-self: flex-end;
        img {
          width: 454px;
          margin: 0 auto;
        }
      }
    }
    .name {
      width: 100%;
      height: 52px;
      text-align: center;
      margin-bottom: 46px;
      img {
        // width: 248px;
        height: 52px;
      }
    }
  }
  .right {
    width: 132px;
    height: 100%;
    padding: 0 44px 182px 0;
  }
  .arrow {
    // width: 11.73333%;
    // height: 11.73333%;
    width: 88px;
    height: 88px;
    z-index: 3;
    img {
      width: 100%;
    }
  }
}
.group2 {
  width: 100%;
  // height: 9.52381%;
  height: 130px;
  // margin-bottom: 9.67262%;
  margin-bottom: 130px;
  text-align: center;
  .flex_func(row, center, center, nowrap);
  .btn {
    width: 440px;
    height: 128px;
    // margin: 0 auto;
    .bg-image("../assets/set_user/btn","png");
  }
}
.mask-layer {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.65);
  z-index: 5;
  position: fixed;
  // .mask-top {
  //   width: 100%;
  //   height: 264px;
  //   // height: 20%;
  //   padding: 0 20px 20px;
  //   .flex_func(row, center, center, nowrap);
  // }
  .mask-con {
    width: 100%;
    height: 100%;
    padding-top: 14px;
    .flex_func(row, center, flex-start, nowrap);
    > img {
      width: 750px;
      height: 692px;
    }
  }
}
.modal {
  text-align: center;
  .content {
    padding-top: 40px;
    padding-bottom: 40px;
    .input {
      width: 85%;
      height: 92px;
      border-radius: 12px;
      padding-left: 15px;
      color: #ccc;
      font-size: 36px;
      .input-placeholder(#ccc, 36px);
      border: 1px solid #fe6f5a;
      :focus {
        outline: 0 !important;
      }
    }
  }
  .footer {
    img {
      width: 348px;
      height: 128px;
    }
  }
}
</style>



